___CSS_LOADER_EXPORT___.push([module.id, ".card-body[data-v-2b641d24] table>tbody>tr>td{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/admin/sub-category/SubCategories.vue"],"names":[],"mappings":"AAuLA,8CACA,cACA","sourcesContent":["<template>\r\n  <div class=\"categories\">\r\n    <div class=\"page-title-box\">\r\n\t\t\t<div class=\"row\">\r\n        <template v-if=\"showErrorMessage || showSuccessMessage\">\r\n            <div class=\"alert\" :class=\"showErrorMessage ? 'alert-danger' : 'alert-success'\" role=\"alert\" v-html=\"message\"></div>\r\n        </template>\r\n\t\t\t\t<div class=\"col\">\r\n\t\t\t\t\t<h4 class=\"page-title\">Sub Category</h4>\r\n\t\t\t\t\t<ol class=\"breadcrumb\">\r\n\t\t\t\t\t\t<li class=\"breadcrumb-item\"><a href=\"/admin/dashboard\">Dashboard</a></li>\r\n\t\t\t\t\t\t<li class=\"breadcrumb-item\"><a href=\"javascript:void(0);\">Sub Categories</a></li>\r\n\t\t\t\t\t\t<li class=\"breadcrumb-item active\">List</li>\r\n\t\t\t\t\t</ol>\r\n\t\t\t\t</div><!--end col-->\r\n\t\t\t\t<div class=\"col-auto align-self-center\">\r\n\t\t\t\t\t<button class=\"btn btn-default\" @click=\"createCategory\">Create</button>\r\n\t\t\t\t</div><!--end col-->  \r\n\t\t\t</div><!--end row-->                                                              \r\n\t\t</div>\r\n\t\t<div class=\"card\">\r\n\t\t\t<div class=\"card-header row\" style=\"margin:0;\">\r\n        <div class=\"col-auto float-end p-0\">\r\n\t\t\t\t\t<input type=\"text\" id=\"search-field\" @keyup=\"search\" placeholder=\"Search by Title\">\r\n\t\t\t\t</div><!--end col-->  \r\n\t\t\t</div>\r\n\t\t\t<div class=\"card-body\">\r\n\t\t\t\t<table class=\"table table-striped\" id=\"search-table\">\r\n\t\t\t\t\t<thead>\r\n\t\t\t\t\t  <tr>\r\n\t\t\t\t\t\t<th scope=\"col\">S.N</th>\r\n\t\t\t\t\t\t<th scope=\"col\">Title</th>\r\n\t\t\t\t\t\t<th scope=\"col\">Category</th>\r\n\t\t\t\t\t\t<th scope=\"col\">Action</th>\r\n\t\t\t\t\t  </tr>\r\n\t\t\t\t\t</thead>\r\n\t\t\t\t\t<tbody>\r\n\t\t\t\t\t  <tr v-for=\"(item, index) in items.data\" :key=\"item.id\">\r\n              <th scope=\"row\">{{ index + 1 }}</th>\r\n              <td>{{ item.title }}</td>\r\n              <td>{{ item.category.title }}</td>\r\n              <td>\r\n                <button class=\"btn btn-sm btn-warning\" @click=\"editCategory( item.id )\"><i class=\"far fa-edit\"></i> Edit</button>\r\n                <button class=\"btn btn-sm btn-danger\" @click=\"deleteCategory( item.id )\"><i class=\"fas fa-trash\"></i> Delete</button>\r\n              </td>\r\n\t\t\t\t\t  </tr>\r\n\t\t\t\t\t</tbody>\r\n\t\t\t\t</table>\r\n        <pagination :data=\"items\" @pagination-change-page=\"getCategories\"></pagination>\r\n\t\t\t</div>\r\n\t\t</div>\t\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from 'axios'\r\nimport Swal from 'sweetalert2'\r\nconst qs = require('querystring');\r\nconst pagination = require('laravel-vue-pagination');\r\n\r\nexport default {\r\n  name: 'SubCategories',\r\n  data: () => {\r\n    return {\r\n      showErrorMessage: false,\r\n      showSuccessMessage: false,\r\n      items: {\r\n        data: []\r\n      },\r\n      message: '',\r\n    }\r\n  },\r\n  computed: {\r\n  },\r\n  methods: {\r\n    createCategory () {\r\n      this.$router.push({path: 'sub-categories/create'});\r\n    },\r\n    editCategoryLink (id) {\r\n      return `sub-categories/${id.toString()}/edit`\r\n    },\r\n    editCategory ( id ) {\r\n      const editCategory = this.editCategoryLink( id );\r\n      this.$router.push({path: editCategory}).catch(error => { console.log });\r\n    },\r\n    deleteCategory ( id ) {\r\n      let self = this;\r\n\r\n      Swal.fire({\r\n        title: 'Are you sure you want to delete sub category?',\r\n        confirmButtonColor: '#3085d6',\r\n        cancelButtonColor: '#d33',\r\n        showCancelButton: true,\r\n        confirmButtonText: 'Okay',\r\n      }).then((result) => {\r\n        /* Read more about isConfirmed, isDenied below */\r\n        if (result.isConfirmed) {\r\n          axios.post(process.env.MIX_APP_URL+'/api/sub-categories/'+id, { _method: 'DELETE' }, {\r\n            headers: {\r\n              'Authorization': 'Bearer '+localStorage.getItem(\"api_token\") \r\n            }\r\n          })\r\n          .then(function (response) {\r\n            if(response.data) {\r\n              Swal.fire({\r\n                title: 'Success',\r\n                text: 'Sub Category deleted successfully',\r\n                confirmButtonText: 'Okay'\r\n              })\r\n              self.getCategories();\r\n            }\r\n          }).catch(function (error) {\r\n            self.$router.push({ path: '/admin/login' });\r\n          });\r\n        }\r\n      })\r\n    },\r\n    \r\n    getCategories (page){\r\n      let self = this;\r\n      if (typeof page === 'undefined') {\r\n          page = 1;\r\n      }\r\n\r\n      axios.get(process.env.MIX_APP_URL+'/api/sub-categories?page='+page, {\r\n\t\t\t\theaders: {\r\n\t\t\t\t\t'Authorization': 'Bearer '+localStorage.getItem(\"api_token\") \r\n\t\t\t\t}\r\n\t\t\t})\r\n      .then(function (response) {\r\n        self.items = response.data.all_subCategories;\r\n      }).catch(function (error) {\r\n        self.$router.push({ path: '/admin/login' });\r\n      });\r\n    },\r\n\r\n    timerMessageHide() {\r\n      setTimeout(() => {\r\n          this.message = '';\r\n          this.showSuccessMessage = false;\r\n          this.showErrorMessage = false;\r\n      }, 2000);\r\n    },\r\n\r\n    search() {\r\n      var input, filter, table, tr, td, i, txtValue;\r\n      input = document.getElementById(\"search-field\");\r\n      filter = input.value.toUpperCase();\r\n      table = document.getElementById(\"search-table\");\r\n      tr = table.getElementsByTagName(\"tr\");\r\n\r\n      // Loop through all table rows, and hide those who don't match the search query\r\n      for (i = 0; i < tr.length; i++) {\r\n        td = tr[i].getElementsByTagName(\"td\")[0];\r\n        if (td) {\r\n          txtValue = td.textContent || td.innerText;\r\n          if (txtValue.toUpperCase().indexOf(filter) > -1) {\r\n            tr[i].style.display = \"\";\r\n          } else {\r\n            tr[i].style.display = \"none\";\r\n          }\r\n        }\r\n      }\r\n    }\r\n  },\r\n  mounted: function(){\r\n    this.getCategories();\r\n  },\r\n  components: {\r\n\t  pagination: pagination,\r\n  },\r\n  metaInfo: {\r\n    // Children can override the title.\r\n    title: 'All Sub Categories',\r\n    // Define meta tags here.\r\n    meta: [\r\n      {name: 'description', content: 'Manage All Sub Category.'}\r\n    ]\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.card-body >>> table > tbody > tr > td {\r\n  cursor: pointer;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
